@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400);
.sortableItem {
	box-shadow: 0 2px 2px rgba(0,0,0,0.15), 0 2px 2px rgba(0,0,0,0.12);
	margin-top: 5px;
	padding: 1em;
}

.attribute {
	padding: 0.5em 0.5em 0.5em 0em;
}

.settings {
	padding: 0 0.8em;

}

