hr {
	border: 0;
	margin: 0;
	padding: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);    
}

.scheme10 {
	display: grid;
	grid-template-columns: repeat(10, 2fr);
}

.scheme20 {
	display: grid;
	grid-template-columns: repeat(20, 2fr);
}
.color-selector {
	/*border: 1px solid black;*/
}

.sequential-picker {
	/*border: 1px solid black;*/
	display: grid;
	grid-template-columns: repeat(6, 1fr);

}
.sequential-picker > * {
	width: 20px;
	height: 20px;
	border-radius: 50%;
	justify-self: center;
}

.example-component {
  width: 100px;
  height: 100px;
  background-color: red;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden; 
}
.example-component.show {
  opacity: 1;
  visibility: visible; 
}

.box {
  visibility: visible;
  height: 12vh;
  width: 100%;
  opacity: 1.0;
  box-sizing: border-box;
  transition: min-height 500ms 0ms, height 500ms 500ms, opacity 500ms 500ms, visibility 500ms 500ms;
}

.box.hide {
  transition: min-height 500ms 500ms, height 500ms 500ms, opacity 500ms 0ms, visibility 500ms 500ms;
  opacity: 0.0;
  visibility: hidden;
  height: 0px;
}

.sidebar {
  margin-top: 0.5em;
  height: 65vh;
  overflow-y: scroll;
  width: 100%;
  opacity: 1.0;
  box-sizing: border-box;
  transition: width 1000ms 0ms, opacity 500ms 500ms;
}

.sidebar.hide {
  /*transition: width 500ms 500ms, opacity 500ms 0ms;*/
  transition: opacity 1000ms 0ms, width 500ms 500ms;
  opacity: 0.0;
  width: 0;
  height: 0;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
body {
	line-height: 1;
	min-height: 100vh;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
	font-family: 'Roboto', sans-serif;
}
.hoverable {
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.hoverable:hover {
	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.ant-table td {
	word-break: break-word;
}
